/* VARIABLES */
/* TYPOGRAPHY */
:root {
  --bs-body-font-family: Sanspro, sans-serif, arial, verdana;
  --bs-body-font-size: 1rem;
  --bs-body-font-weight: 400;
  --bs-body-line-height: 1.563;
}

/* Inter font from https://github.com/rsms/inter/releases */
@font-face {
  font-display: swap;
  font-family: Inter;
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/inter/Inter-Regular.woff2?v=3.15") format("woff2"), url("../fonts/inter/Inter-Regular.woff?v=3.15") format("woff");
}
@font-face {
  font-display: swap;
  font-family: Inter;
  font-style: italic;
  font-weight: 400;
  src: url("../fonts/inter/Inter-Italic.woff2?v=3.15") format("woff2"), url("../fonts/inter/Inter-Italic.woff?v=3.15") format("woff");
}
@font-face {
  font-display: swap;
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/inter/Inter-SemiBold.woff2?v=3.15") format("woff2"), url("../fonts/inter/Inter-SemiBold.woff?v=3.15") format("woff");
}
