  font-display: swap;
  font-family: Inter;
  font-style: italic;
  font-weight: 600;
  src: url("../fonts/inter/Inter-SemiBoldItalic.woff2?v=3.15") format("woff2"), url("../fonts/inter/Inter-SemiBoldItalic.woff?v=3.15") format("woff");
}
