.ck-content {
  color: #052d4d;
}
.ck-content h1,
.ck-content h2,
.ck-content h3,
.ck-content h4,
.ck-content h5,
.ck-content h6 {
  font-weight: 700;
  line-height: 1.2;
}
.ck-content h1 {
  font-size: 48px;
}
@media (max-width: 991px) {
  .ck-content h1 {
    font-size: 24px;
  }
}
@media (max-width: 480px) {
  .ck-content h1 {
    font-size: 20px;
  }
}
.ck-content h2 {
  font-size: 24px;
}
@media (max-width: 991px) {
  .ck-content h2 {
    font-size: 20px;
  }
}
@media (max-width: 480px) {
  .ck-content h2 {
    font-size: 18px;
  }
}
.ck-content h3 {
  font-size: 20px;
}
@media (max-width: 991px) {
  .ck-content h3 {
    font-size: 18px;
  }
}
@media (max-width: 480px) {
  .ck-content h3 {
    font-size: 16px;
  }
}
.ck-content h4 {
  font-size: 18px;
}
@media (max-width: 991px) {
  .ck-content h4 {
    font-size: 16px;
  }
}
@media (max-width: 480px) {
  .ck-content h4 {
    font-size: 14px;
  }
}
.ck-content h5 {
  font-size: 16px;
}
@media (max-width: 480px) {
  .ck-content h5 {
    font-size: 14px;
  }
}
.ck-content h6 {
  font-size: 14px;
}
.ck-content .bg-inverse {
  background: #000 !important;
}